.accordion {
  margin-top: 1rem;
  margin: 1rem;
  border-bottom: 1px solid black;
}

.topic {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: 1px solid black;
  width: 100%;
  min-height: 2rem;
  border-bottom: none;
  font-size: 120%;
}

.answer {
  border-left: 1px solid black;
  border-right: 1px solid black;
  margin: 0;
  padding: 1rem;
}

.answer ul {
  padding: 1rem;
  margin: 0;
}

.answer h2 {
  text-align: center;
}

.answer h3 {
  margin: 0;
}

.first-text {
  padding-top: 1rem;
}

.topic:last-of-type {
  border-bottom: 0px solid black;
}

.answer p:first-of-type {
  margin-top: 0;
}

.answer p:last-of-type {
  margin-bottom: 0;
  border-bottom: 0px solid black;
}

.last {
  border-bottom: 1px solid black;
}

.accessibility-photo-div {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.accessibility-photo {
  width: auto;
  height: auto;
  max-width: 100%;
}

.text-center {
  text-align: center;
}