.privacy {
  padding: 1rem;
}

.privacy-title {
  text-align: center;
}

.privacy-columns {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.privacy-column {
  grid-column: span 4;
  padding: 1rem;
}

@media screen and (max-width:44rem) {
  .privacy-columns {
    display: flex;
    flex-direction: column;
  }
}