.cards {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.card {
  border: 1px solid black;
  padding: 0;
  margin: 2rem;
  background-color: rgb(237 237 237);
  display: flex;
  flex-direction: column;
  align-items: center;

}

.card-img {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.card-cta {
  margin-bottom: 1rem;
  font-size: 1.05rem;
  color: white;
  text-decoration: none;
  border: none;
  background-color: rgb(196, 0, 46);
  display: inline-block;
  text-align: center;
  line-height: 1rem;
  min-width: 8rem;
  padding: 1rem;
}

.card-cta:focus {
  background-color: rgb(138,111,46);
}

@media (prefers-reduced-motion: no-preference) {
  .card-cta {
    transition: box-shadow 0.4s cubic-bezier(.44, .47, .58, .9);
  }

  .card-cta:hover {
    box-shadow: inset 0 -4rem rgb(137, 0, 40);
  }
}

@media screen and (max-width : 50rem) {
  .card {
    grid-column: span 12;
    align-items: center;
    width: fit-content;
  }

  .cards {
    justify-items: center;
  }
}

@media screen and (min-width : 50rem) and (max-width : 63rem) {
  .card {
    grid-column: span 6;
    width: fit-content;
  }
}

@media screen and (min-width : 63rem) {
  .card {
    grid-column: span 4;
    align-items: center;
    width: fit-content;
  }
}