.form {
  display: grid;
  grid-template-areas:
    " name email confirm"
    "checkbox checkbox checkbox"
    "human human human "
    " form-button  form-button form-button";
  grid-template-columns: auto auto auto;
}

.form label {
  padding-bottom: 1rem;
}

.name {
  grid-area: name;
}

.email {
  grid-area: email;
}

.confirm {
  grid-area: confirm;
}

.checkbox {
  grid-area: checkbox;
}

.name-input,
.email-input,
.confirm-input {
  min-width: 1rem;
  min-height: 1rem;
}

.open {
  grid-area: human;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  margin: 0.5rem;
  border: 1px solid black;
  background-color: rgb(237, 237, 237);
}

textarea {
  resize: none;
  min-width: 15rem;
  min-height: 10rem;
}

.form-button {
  grid-area: form-button;
  font-size: 1.0rem;
  color: white;
  text-decoration: none;
  border: none;
  cursor: pointer;
  background-color: rgb(196, 0, 46);
  display: inline-block;
  text-align: center;
  line-height: 1rem;
  max-width: 8rem;
  min-width: 8rem;
  padding: 1rem;
}

.form-button:hover {
  background-color: rgb(138, 111, 46)
}

.failed-closed {
  display: none;
}

.failed {
  display: initial;
  color: red;
}

@media screen and (max-width:44rem) {
  .form {
    display: grid;
    grid-template-areas:
      " name "
      "email "
      "confirm"
      "checkbox "
      "human "
      " form-button ";
    grid-template-columns: auto;
  }

  .name,
  .email,
  .confirm,
  .message,
  .human {
    display: flex;
    flex-direction: column;
  }

}