header {
  background-color: rgb(74, 74, 74);
  color: white;
  display: grid;
  grid-template-areas:
    "header-logo header-title"
    "header-nav header-nav";
  grid-template-columns: auto 1fr;
}

.header-logo {
  grid-area: header-logo;
  cursor: pointer;
}

.header-title {
  grid-area: header-title;
  padding: 0.25rem;
  text-align: center;
}

.header-nav {
  grid-area: header-nav;
}
