.gallery-buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.gallery-button-next {
  margin-right: 0.5rem;
  background-color: rgb(20 148 159);
  color: white;
  text-align: center;
  padding: 0.5rem;
}

.gallery-button-reset {
  margin-left: 0.5rem;
  background-color: rgb(20 148 159);
  color: white;
  text-align: center;
  padding: 0.5rem;
}

.gallery-buttons-notice {
  text-align: center;
  padding: 0.5rem;
}

.gallery-cards {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.gallery-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: fit-content;
  margin: auto;
  border: 1px solid black;
  padding: 1rem;
  padding-bottom: 0;
  margin: 0;
  background-color: rgb(237 237 237);
}

.card-text-div {
  display: flex;
}

.card-text {
  text-align: center;
}

.card-img-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-img {
  width: auto;
  height: auto;
  max-width: 100%;
}

.open {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.close {
  display: none;
}