.global-nav-list {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 0;
  margin: 0;
  list-style: none;
}

.global-nav-item{
  padding: 0.5rem;
}

/* .global-nav-item:focus-within{
  background-color: rgb(156 129 60);
} */

.global-nav-link {
  display: inline-block;
  padding: 0.5rem 1rem;
  color: white;
  text-decoration: none;
}

.global-nav-item:hover,
.global-nav-link:hover{
  background-color: rgb(156 129 60);
}

.drop {
  padding: 0;
  display: none;
  position: absolute;
  min-width: 5rem;
}

.global-nav-item:hover .drop,
.global-nav-item:focus-within .drop
 {
  display: block;
  background-color: brown;
  list-style: none;
}

@media screen and (min-width : 44rem) {

  .global-nav-list {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 0;
    margin: 0;
    list-style: none;
    color: white;
  }

}