.panel {
  border: 1px solid black;
  padding: 0;
  margin: 0;
  background-color: rgb(237 237 237);
  border-bottom: none;
  padding-bottom: 1px;
}

.panel:last-of-type {
  border-bottom: 1px solid black;
}

.panelimg-div {
  grid-area: img;
  padding: 0.5rem;
}

.panel-title {
  text-align: center;
  margin-bottom: 0;
  grid-area: panel-title;
}

.panel-fragment {
  grid-area: panel-fragment;
}

.panel-fragment p {
  margin: 0;
  padding: 1rem;
  line-height: 1.5;
}

@media screen and (max-width:62rem) {

  .panelimg-div {
    float: right;
  }

  .panelimg {
    display: flex;
    width: auto;
    height: auto;
    max-width: 100%;
  }

}

@media screen and (min-width:62rem) {

  .panel {
    display: grid;
    grid-template-areas:
      "panel-title img"
      "panel-fragment img";
    grid-template-columns: auto auto;
  }

  .panel {
    padding: 1rem;
    border: 1px solid black;
    background-color: rgb(196 196 196);
    margin: 2rem;
  }

  .panelimg-div {
    display: flex;
    aspect-ratio: 3/2;
    grid-area: img;
    padding: 0.5rem;
  }

  .panelimg {
    display: flex;
    aspect-ratio: 3/2;
  }

}