.footer {
  background-color: rgb(38, 38, 38);
  color: white;
  padding: 1rem;
}

.footer-list {
  display: none;
  padding: 1rem;
  margin: 0;
  list-style: none;
}

.footer-list li {
  padding: 0.5rem;
}

.footer-list li:hover {
  background-color: rgb(156 129 60);
}

/* .footer-list li:focus-within {
  background-color: rgb(156 129 60);
} */

.footer-nav-link {
  color: white;
  text-decoration: none;
}

.footer-nav-link:hover {
  background-color: rgb(156 129 60);
}

.footer-nav-button {
  padding: 0.5rem;
  margin: 0.5rem;
  background-color: rgb(54 68 77);
  color: white;
  text-decoration: none;
}

.footer-list-open {
  display: block;
}

@media screen and (min-width : 44rem) {

  .footer-nav-button {
    display: none;
  }

  .footer-list {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 1rem;
    margin: 0;
    list-style: none;
  }

}